.search-div {
  display: flex;
  justify-content: center;
  margin: 20px 0px;
}

.search-component-div {
  /* padding: 5px;
  margin: 5px; */
  display: flex;
  /* width: 50%; */
}

.search-box {
  padding: 10px;
  width: 100%;
  border-radius: 5px;
  border: 0px solid black;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
    rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.search-recommendation-div {
  width: inherit;
  flex-direction: column;
  position: absolute;
  margin-top: 3rem;
  padding: 5px;
  border: 2px;
  background-color: white;
  overflow-y: scroll;
  max-height: 25%;
  box-shadow: inset 0px 0px 0px 1px #d3d3d3;
}

.search-recommendation-div::-webkit-scrollbar{
  width: 8px;
  background-color:rgb(235, 235, 235);
  border-radius: 10px;
}

.search-recommendation-div::-webkit-scrollbar-thumb{
  width: 5px;
  background-color: rgb(202, 202, 202);
  border-radius: 10px;
}





.search-results {
  padding: 5px;
  margin: 5px 0px;
}

.chartSection {
  display: flex;
  width: 100%;
  height: 80%;
  padding: 25px;
  justify-content: center;
}

.SectionHolderDiv {
  display: flex;
  justify-content: center;
  flex-direction: row;
  width: 100%;
  height: 60vh;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.dataDiv {
  width: 100%;
  padding: 2%;
  display: flex;
  flex-direction: column;
}

.chartDiv {
  width: 100%;
  padding: 2%;
}

.dataHeaders {
  padding: 25px 50px;
}

.subHeadersDiv {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  padding: 0px 50px;
}

.labels {
  display: flex;
  flex-direction: column;
  width: 50%;
}

.labelTextDiv {
  margin: 4px;
  display: flex;
  color: rgba(111, 137, 150, 1);
}

.values {
  display: flex;
  flex-direction: column;
  width: 50%;
}

.valuesTextDiv {
  margin: 4px;
  display: flex;
}

.googleMapLinkDiv {
  display: flex;
  padding: 10px 54px;
  font-size: larger;
  cursor: pointer;
  align-items: center;
  color: rgba(4, 171, 255, 1);
  text-decoration: none;
}

.viewOnMap {
  margin-right: 2%;
  color: rgba(4, 171, 255, 1);
  text-decoration: none;
}
